/*
LESS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: @icon-home-width;
}

The large array-like variables contain all information about a single icon
@icon-home: x y offset_x offset_y width height total_width total_height image_path name;

At the bottom of this section, we provide information about the spritesheet itself
@spritesheet: width height image @spritesheet-sprites;
*/
@header-icon-down-name: 'header-icon-down';
@header-icon-down-x: 78px;
@header-icon-down-y: 0px;
@header-icon-down-offset-x: -78px;
@header-icon-down-offset-y: 0px;
@header-icon-down-width: 49px;
@header-icon-down-height: 49px;
@header-icon-down-total-width: 174px;
@header-icon-down-total-height: 99px;
@header-icon-down-image: '../img/sprite.png';
@header-icon-down: 78px 0px -78px 0px 49px 49px 174px 99px '../img/sprite.png' 'header-icon-down';
@header-logo-name: 'header-logo';
@header-logo-x: 0px;
@header-logo-y: 0px;
@header-logo-offset-x: 0px;
@header-logo-offset-y: 0px;
@header-logo-width: 68px;
@header-logo-height: 55px;
@header-logo-total-width: 174px;
@header-logo-total-height: 99px;
@header-logo-image: '../img/sprite.png';
@header-logo: 0px 0px 0px 0px 68px 55px 174px 99px '../img/sprite.png' 'header-logo';
@header-mail-name: 'header-mail';
@header-mail-x: 0px;
@header-mail-y: 65px;
@header-mail-offset-x: 0px;
@header-mail-offset-y: -65px;
@header-mail-width: 49px;
@header-mail-height: 34px;
@header-mail-total-width: 174px;
@header-mail-total-height: 99px;
@header-mail-image: '../img/sprite.png';
@header-mail: 0px 65px 0px -65px 49px 34px 174px 99px '../img/sprite.png' 'header-mail';
@header-phone-name: 'header-phone';
@header-phone-x: 137px;
@header-phone-y: 0px;
@header-phone-offset-x: -137px;
@header-phone-offset-y: 0px;
@header-phone-width: 37px;
@header-phone-height: 37px;
@header-phone-total-width: 174px;
@header-phone-total-height: 99px;
@header-phone-image: '../img/sprite.png';
@header-phone: 137px 0px -137px 0px 37px 37px 174px 99px '../img/sprite.png' 'header-phone';
@spritesheet-width: 174px;
@spritesheet-height: 99px;
@spritesheet-image: '../img/sprite.png';
@spritesheet-sprites: @header-icon-down @header-logo @header-mail @header-phone;
@spritesheet: 174px 99px '../img/sprite.png' @spritesheet-sprites;

/*
The provided classes are intended to be used with the array-like variables

.icon-home {
  .sprite-width(@icon-home);
}
.icon-email {
  .sprite(@icon-email);
}

Example usage in HTML:

`display: block` sprite:
<div class="icon-home"></div>

To change `display` (e.g. `display: inline-block;`), we suggest using a common CSS class:

// CSS
.icon {
  display: inline-block;
}

// HTML
<i class="icon icon-home"></i>
*/
.sprite-width(@sprite) {
  width: extract(@sprite, 5);
}

.sprite-height(@sprite) {
  height: extract(@sprite, 6);
}

.sprite-position(@sprite) {
  @sprite-offset-x: extract(@sprite, 3);
  @sprite-offset-y: extract(@sprite, 4);
  background-position: @sprite-offset-x @sprite-offset-y;
}

.sprite-image(@sprite) {
  @sprite-image: extract(@sprite, 9);
  @sprite-image-bare: ~`"@{sprite-image}".slice(1, -1)`;
  background-image: url(@sprite-image-bare);
}

.sprite(@sprite) {
  .sprite-image(@sprite);
  .sprite-position(@sprite);
  .sprite-width(@sprite);
  .sprite-height(@sprite);
}

/*
The `.sprites` mixin generates identical output to the CSS template
  but can be overridden inside of LESS

This must be run when you have at least 2 sprites.
  If run with a single sprite, then there will be reference errors.

.sprites(@spritesheet-sprites);
*/
.sprites(@sprites, @i: 1) when (@i <= length(@sprites)) {
  @sprite: extract(@sprites, @i);
  @sprite-name: e(extract(@sprite, 10));
  .@{sprite-name} {
    .sprite(@sprite);
  }
  .sprites(@sprites, @i + 1);
}
