.first-section{
	position: relative;
	height: 100%;
	background-image: url("../img/body-bg.jpg");
	background-attachment: fixed;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	@media screen and (max-width: 600px) {
		//height: auto;
		min-height: 650px;
	}
	&__wrap{
		position: relative;
		height: 100%;
		background: linear-gradient(to right, 
			rgba(255,255,255,.75) 0%,
			rgba(255,255,255,.9) 25%,
			rgba(255,255,255,.9) 75%,
			rgba(255,255,255,.75) 100%,
			);
	}
	.valign-container{
		position: absolute;
		top: 50%;
		left: 50%;
		width: 100%;
		transform: translate(-50%, -50%);
	}
	.logo-slogan{
		@media screen and (min-width: @tablet) {
			margin-bottom: 25px;
		}
		@media screen and (min-width: @large) {
			margin-bottom: 80px;
		}
	}
	.logo{
		text-align: center;
		&__img{
			.inline-block;
			width: 272px;
			height: 182px;
			@media screen and (max-width: 600px) {
				width: 180px;
				height: 120px;
				margin-bottom: 15px;
			}
		}
		&__title{
			font-size: 60px;
			line-height: 72px;
			color: @textDarkBlue;
			@media screen and (max-width: 600px) {
				font-size: 40px;
			}
		}
	}
	.main-slogan{
		text-align: center;
		color: @blue;
		@media screen and (min-width: @tablet) {
			padding-top: 157px;
		}
		&__title{
			font-size: 36px;
			line-height: 36px;
			@media screen and (max-width: 600px) {
				font-size: 24px;
			}
		}
		&__text{
			.dinpromedium;
			font-size: 24px;
			line-height: 24px;
			@media screen and (max-width: 600px) {
				margin-bottom: 20px;
				font-size: 18px;
			}
		}
	}
	.btn{
		margin-bottom: 10px;
		&:hover{
			background: darken(@blue, 5%);
		}
	}
	.icon-down{
		position: absolute;
		bottom: 25px;
		left: 50%;
		width: 50px;
		height: 50px;
		transform: translate(-50%, 0);
		.header-icon-down;
	}
}