.main-header{
	position: fixed;
	width: 100%;
	.line-height(75px);
	color: @white;
	background: @blue;
	z-index: 100;
	.container{
		position: relative;
	}
	.close-menu{
		float: left;
		margin-top: 24px;
		width: 30px;
		height: 25px;
		cursor: pointer;
		@media screen and (min-width: @large) {
			display: none;
		}
		&__item{
			margin-bottom: 5px;
			width: 100%;
			height: 5px;
			background: @white;
			border-radius: 10px;
		}
	}
	.header-logo{
		position: absolute;
		width: 68px;
		height: 55px;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		.icon-header-logo;
		@media screen and (min-width: @large) {
			left: 0;
			transform: translate(0, -50%);
		}
	}
	.actions{
		float: right;
		min-width: 100px;
		&__item{
			.inline-block;
		}
		.phone{
			.icon-header-phone;
			margin-right: 19px;
		}
		.mail{
			.icon-header-mail;
		}
	}
	.main-menu{
		position: absolute;
		top: 74px;
		left: 0;
		width: 100%;
		padding-left: 100px;
		display: none;
		font-size: 0;
		background: @blue;
		@media screen and (min-width: @large) {
			position: static;
			display: block !important;
		}
		&__item{
			position: relative;
			.dinpromedium;
			display: block;
			height: 100%;
			padding: 0 35px;
			font-size: 18px;
			color: @textBlack;
			transition: all .5s;
			@media screen and (min-width: @large) {
				.inline-block;
			}
			&::before{
				content: '';
				position: absolute;
				top: 47%;
				left: -3px;
				width: 6px;
				height: 6px;
				background: @white;
				border-radius: 50%;
			}
			&:hover{
				color: @white;
				background: @darkBlue;
			}
		}
	}
}