* {
    padding: 0;
    margin: 0;
}

html,
body {
    height: 100%;
}

body {
    min-width: 320px;
    .dinproregular;
    font-weight: normal;
    font-size: 18px;
    color: @black;
    background: white;
}

a:visited,
a:link,
a:hover {
    text-decoration: none;
}
.clearfix::after{
	content: '';
	display: block;
	clear: both;
}
img{
	max-width: 100%;
	height: auto;
}
ul{
	margin: 0;
	padding: 0;
	list-style-type: none;
}
h2{
	font-size: 48px;
	color: #fff;
}
.btn{
	width: 100%;
	.line-height(45px);
	font-size: 18px;
	text-align: center;
	color: @white;
	cursor: pointer;
	&__blue{
		background: @blue;
	}
}