/* header 
=========================================*/
.main-header {
  position: fixed;
  width: 100%;
  height: 75px;
  line-height: 75px;
  color: #fff;
  background: #3e91c9;
  z-index: 100;
}
.main-header .container {
  position: relative;
}
.main-header .close-menu {
  float: left;
  margin-top: 24px;
  width: 30px;
  height: 25px;
  cursor: pointer;
}
@media screen and (min-width: 1170px) {
  .main-header .close-menu {
    display: none;
  }
}
.main-header .close-menu__item {
  margin-bottom: 5px;
  width: 100%;
  height: 5px;
  background: #fff;
  border-radius: 10px;
}
.main-header .header-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url(../img/sprite.png);
  background-position: 0px 0px;
  width: 68px;
  height: 55px;
}
@media screen and (min-width: 1170px) {
  .main-header .header-logo {
    left: 0;
    transform: translate(0, -50%);
  }
}
.main-header .actions {
  float: right;
  min-width: 100px;
}
.main-header .actions__item {
  display: inline-block;
  vertical-align: middle;
}
.main-header .actions .phone {
  background-image: url(../img/sprite.png);
  background-position: -137px 0px;
  width: 37px;
  height: 37px;
  margin-right: 19px;
}
.main-header .actions .mail {
  background-image: url(../img/sprite.png);
  background-position: 0px -65px;
  width: 49px;
  height: 34px;
}
.main-header .main-menu {
  position: absolute;
  top: 74px;
  left: 0;
  width: 100%;
  padding-left: 100px;
  display: none;
  font-size: 0;
  background: #3e91c9;
}
@media screen and (min-width: 1170px) {
  .main-header .main-menu {
    position: static;
    display: block !important;
  }
}
.main-header .main-menu__item {
  position: relative;
  font-family: 'dinpromedium';
  display: block;
  height: 100%;
  padding: 0 35px;
  font-size: 18px;
  color: #000;
  transition: all .5s;
}
@media screen and (min-width: 1170px) {
  .main-header .main-menu__item {
    display: inline-block;
    vertical-align: middle;
  }
}
.main-header .main-menu__item::before {
  content: '';
  position: absolute;
  top: 47%;
  left: -3px;
  width: 6px;
  height: 6px;
  background: #fff;
  border-radius: 50%;
}
.main-header .main-menu__item:hover {
  color: #fff;
  background: #367fb1;
}
